export default{
	methods:{
		 styleColor:function(con,comper){
            console.log(con,"--------",comper)
			console.log(con<comper)
            return con<comper? "sub-color":(con==comper?"":"add-color");
        },
		styleColorByBiz:function(biz){
			if(biz==global.STKBIZ.BUY_IN || biz==global.STKBIZ.SUBSCRIBE_PURCHASE ){
				return "add-color";
			}else if(biz==global.STKBIZ.SELL_OUT ){
				return "sub-color";
			}
			return ''
		},
        goBack(){
        	this.$router.go(-1);
        }
	}
}



