<template lang="html">
  <div class="_full_router changeMbBody">
    <custHeader :title="title"></custHeader>
    <div class="" v-show="firstVis">
      <mt-field label="交易密码" placeholder="请输入交易密码" type="password" v-model="password" ></mt-field>
      <mt-field label="验证码" placeholder="请输入验证码" v-model="captcha"><mt-button size="small" type="primary" :disabled=idDisabled @click="send">{{ capMessage }}</mt-button></mt-field>
      <mt-button  type="primary" size="large"  @click="firstCheck">下一步</mt-button>
    </div>
    <div class="" v-show="secondVis">
      <mt-field label="新手机号"  placeholder="请输入新的手机号" v-model="newMb" v-validtype="rules.newMb" :state="rules.newMb.state"></mt-field>
      <mt-field label="验证码" placeholder="请输入验证码" v-model="captchaNew"><mt-button size="small" type="primary" :disabled=idDisabledNew @click="sendNew">{{ capMessageNew }}</mt-button></mt-field>
      <mt-button  type="primary" size="large"  @click="secondCheck">确定</mt-button>
    </div>
  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {MessageBox,Field} from 'mint-ui'
import cookieUtil from 'js-cookie'
export default {
  data(){
    return {
      rules:{
        newMb:{
          str:"required,mobile",
          state:''
        }
      },
      title:'更改手机号',
      password:'',
      capMessage:"发送短信",
      capMessageNew:'发送短信',
      captcha:'',
      captchaNew:'',
      newMb:'',
      idDisabled:false,
      idDisabledNew:false,
      firstVis:true,
      secondVis:false
    }
  },
  methods:{
    firstCheck:function(){
      var self = this
      if (global.isOpenMd5) {
        this.password = global.md5(this.password);
      }
      axiosUtil.upCltPassAndSmsCheck({password:self.password,smsAuthCode: this.captcha,}).then(data => {
        var data = data.data;
        if(data.errorcode == 0){
          self.firstVis = false
          self.secondVis = true
        }else{
          MessageBox("提示",data.errormsg)
        }
      })
    },
    secondCheck:function(){
      var self = this
      axiosUtil.upCltCustMbBind({mb:self.newMb,smsAuthCode: this.captchaNew,}).then(data => {
        var data = data.data;
        if(data.errorcode == 0){
          cookieUtil.remove("mb")
          cookieUtil.set("mb", self.newMb,{expires:1,path:global.webSite})
          MessageBox("提示",data.errormsg)
          router.push("/personal/userInfo")
        }else{
          MessageBox("提示",data.errormsg)
        }
      })
    },
    sendNew:function(){
      var that = this;
      // this.mb = cookieUtil.get("mb");
      if(!(this.rules.newMb.state !== 'error' &&  this.rules.newMb.state !== '' && this.newMb)){
        MessageBox("提示", "你的手机号格式不正确");
        return false
      }
      axiosUtil.sendSms({
        askBizCode: 100869,
        mb:this.newMb
      }).then(function(data){
        if (data.errorcode == 1) {
          MessageBox("提示", data.errormsg);
        } else{
          let time = 60;
          that.capMessageNew = time + "s后重新发送"
          that.idDisabledNew = true;
          let timer = setInterval(function(){
            time = time - 1;
            that.capMessageNew = time + "s后重新发送";
            if(!time){
              // time = 60
              that.capMessageNew = '发送短信'
              that.idDisabledNew = false;
              clearInterval(timer)
            }
          },1000)
        }
      })
    },
    send:function(){
      var that = this;
      this.mb = cookieUtil.get("mb");
      axiosUtil.sendSms({
        askBizCode: 100870,
        mb:this.mb
      }).then(function(data){
        if (data.errorcode == 1) {
          MessageBox("提示", data.errormsg);
        } else{
          let time = 60;
          that.capMessage = time + "s后重新发送"
          that.idDisabled = true;
          let timer = setInterval(function(){
            time = time - 1;
            that.capMessage = time + "s后重新发送";
            if(!time){
              // time = 60
              that.capMessage = '发送短信'
              that.idDisabled = false;
              clearInterval(timer)
            }
          },1000)
        }
      })

    },
  },
  components:{
     custHeader
  },
}
</script>

<style lang="css">
</style>



